<script>
import upload from "@/components/Upload/Upload.vue";
export default {
  name: "newProjectApplication-component",
  components: { upload },
  data() {
    // 最小日期加上一年的時間要小於最大日期
    let validateDate = (rule, value, callback) => {
      if (value) {
        const minDateTime = value[0].getTime();
        const maxDateTime = value[1].getTime();
        if (minDateTime + 31536000000 < maxDateTime) {
          callback(new Error("施工期限最大一年 "));
        } else {
          callback();
        }
      } else {
        callback(new Error("必填"));
      }
    };
    return {
      createDate: this.formatDate(new Date()),
      signerLoading: false,
      sIdLoading: false,
      sId2Loading: false,
      projectNumber: "",
      supplier1: [],
      supplier2: [],
      workTypeLists: [],
      form: {
        project_Name: "", // 工程名稱
        LD1: "", // 廠區名稱
        Loaction_Type: "", // 無塵室/非無塵室
        FloorId: [], // FAB 廠別/區域 (依上方選項帶出施工區域)
        Work_Type_Class: [],
        Work_Type_Class_Other: [],
        Description: "", // 工程內容
        date: "", // 工程開始～結束日期
        S_Id: "", // 主承包商公司名稱
        S_Id2: [], // 下包商公司名稱
        Project_Apply: "", // 工程單請求編號radio
        Project_Apply_No: "", // 工程單請求編號
        Project_Apply_Remark: "", // 廠務發包工程請填入工程申請單號
        confirm: [],
        File1: null, // 工程前安全會議紀錄
        File2: null, // 施工防護企畫書
        Isbuild: "", // 本工程單是否為營建工程
        Signer: [], // 每日工作申請單發包工程師代簽人員
      },
      rules: {
        project_Name: [{ required: true, message: "必填", trigger: "blur" }],
        LD1: [{ required: true, message: "必填", trigger: "change" }],
        Loaction_Type: [{ required: true, message: "必填", trigger: "change" }],
        FloorId: [{ required: true, message: "必填", trigger: "change" }],
        Work_Type_Class: [
          { required: false, message: "必填", trigger: "change" },
        ],
        Work_Type_Class_Other: [
          { required: false, message: "必填", trigger: "change" },
        ],
        Description: [{ required: true, message: "必填", trigger: "blur" }],
        date: [{ required: true, message: "必填", trigger: "change" }],
        S_Id: [{ required: true, message: "必填", trigger: "change" }],
        Project_Apply: [{ required: false }],
        Project_Apply_No: [
          { required: false, message: "必填", trigger: "change" },
        ],
        Project_Apply_Remark: [
          { required: true, message: "必填", trigger: "blur" },
        ],
        confirm: [{ required: true, message: "必填", trigger: "change" }],
        File1: [{ required: true, message: "必填", trigger: "change" }],
        File2: [{ required: false, message: "必填", trigger: "change" }],
        Isbuild: [{ required: true, message: "必填", trigger: "change" }],
        Signer: [{ required: false, message: "必填", trigger: "change" }],
      },
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          if ((minDate, maxDate)) {
            this.rules.date = { validator: validateDate, trigger: "change" };
          }
        },
      },
      fabApprove: [],
      location: [],
      location_Type: [],
      work_Type_Class: [],
      signerList: [],
    };
  },
  computed: {
    fabApproveList() {
      const data = this.fabApprove.filter(
        (e) =>
          e.ld1 === Number(this.form.LD1) &&
          e.typeId === Number(this.form.Loaction_Type)
      );
      return data;
    },
    // 高風險把其他設定排除出去的資料
    workTypeClassList() {
      this.work_Type_Class.forEach((e, i) => {
        e.highRiskWorkNumber = `highRiskWork_${i + 1}`;
      });
      let data = JSON.parse(JSON.stringify(this.work_Type_Class));
      data.pop();
      return data;
    },
    // 高風險的其他設定資料
    workTypeClassOtherList() {
      let data = JSON.parse(JSON.stringify(this.work_Type_Class));
      const newData = data.pop();
      return newData;
    },
  },
  methods: {
    // 廠務發包工程/非廠務發包工程動態表單驗證
    engineerOrderNumberHandler(e) {
      if (e === "2") {
        this.form.Project_Apply_No = "";
        this.rules.Project_Apply_Remark = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.Project_Apply = [
          { required: true, message: "必填", trigger: "blur" },
        ];
      } else {
        this.form.Project_Apply_Remark = "";
        this.rules.Project_Apply_Remark = [];
        this.rules.Project_Apply = [];
        this.$refs.form.clearValidate("Project_Apply_No");
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 建立新增資料物件
          const formGroup = {
            project_Name: this.form.project_Name,
            Signer: this.form.Signer,
            LD1: this.form.LD1,
            Loaction_Type: this.form.Loaction_Type,
            FloorId: this.form.FloorId,
            Work_Type_Pk: this.form.Work_Type_Class.concat(
              this.form.Work_Type_Class_Other
            ),
            Description: this.form.Description,
            Begin_Date: this.formatDate(this.form.date[0]),
            End_Date: this.formatDate(this.form.date[1]),
            S_Id: this.form.S_Id,
            S_Id2: this.form.S_Id2,
            Project_Apply_No: this.form.Project_Apply_No,
            Project_Apply_Remark: this.form.Project_Apply_Remark,
            Isbuild: this.form.Isbuild,
            File1: this.form.File1,
            File2: this.form.File2,
          };
          this.addNewProjectApplication(formGroup);
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: `資料填寫錯誤`,
          });
          this.highRiskWorkChangeHandler();
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 高風險動態表單驗證
    highRiskWorkChangeHandler() {
      if (
        this.form.Work_Type_Class.length ||
        this.form.Work_Type_Class_Other.length
      ) {
        this.rules.Work_Type_Class = [];
        this.rules.Work_Type_Class_Other = [];
        this.$refs.form.clearValidate("Work_Type_Class");
        this.$refs.form.clearValidate("Work_Type_Class_Other");
      } else {
        this.rules.Work_Type_Class = [
          { required: false, message: "必填", trigger: "change" },
        ];
        this.rules.Work_Type_Class_Other = [
          { required: false, message: "必填", trigger: "change" },
        ];
        this.$refs.form.validateField("Work_Type_Class");
        this.$refs.form.validateField("Work_Type_Class_Other");
      }
    },
    // 高風險上傳欄位動態表單驗證
    file2ChangeHandler(evt, detail) {
      if (evt) {
        this.workTypeLists.push(detail);
      } else {
        this.workTypeLists = this.workTypeLists.filter(
          (e) => e.work_Type_Pk !== detail.work_Type_Pk
        );
      }
      const res = this.workTypeLists.some((e) => e.cp_Plan === "Y");
      if (res) {
        this.rules.File2 = [
          { required: true, message: "必填", trigger: "change" },
        ];
      } else {
        this.rules.File2 = [];
        this.$refs.form.clearValidate("File2");
      }
    },
    meetRecordFileChangeHandler(fileList) {
      const fileSize = fileList[0].size > 2 * 1024 * 1024;
      const fileFormat = fileList[0].raw.type;
      const isPdf = fileFormat === "application/pdf";
      if (fileSize || !isPdf) {
        this.$message({
          type: "error",
          duration: 5000,
          showClose: true,
          message: "檔案不可大於2MB且檔案格式必須為PDF",
        });
        // 不允許檔案被上傳
        this.$refs.File1.$refs.Upload.clearFiles();
      } else {
        this.form.File1 = fileList[0].raw;
        this.$refs.form.clearValidate("File1");
      }
    },
    meetRecordFileRemoveHandler(fileList) {
      this.form.File1 = fileList;
      this.$refs.form.validateField("File1");
    },
    constructionPlanChangeHandler(fileList) {
      const fileSize = fileList[0].size > 2 * 1024 * 1024;
      const fileFormat = fileList[0].raw.type;
      const isPdf = fileFormat === "application/pdf";
      if (fileSize || !isPdf) {
        this.$message({
          type: "error",
          duration: 5000,
          showClose: true,
          message: "檔案不可大於2MB且檔案格式必須為PDF",
        });
        // 不允許檔案被上傳
        this.$refs.File2.$refs.Upload.clearFiles();
      } else {
        this.form.File2 = fileList[0].raw;
        this.$refs.form.clearValidate("File2");
      }
    },
    constructionPlanRemoveHandler(fileList) {
      this.form.File2 = fileList;
      this.$refs.form.validateField("File2");
    },
    // 清除遠端搜尋資料
    visibleHandleChange(e) {
      if (!e) {
        this.signerList = [];
        this.$refs.remote.query = "";
      }
    },
    signerSearchHandler(evt) {
      this.signerLoading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.throttledSignerSearch(evt);
      }
    },
    //2024.03.13 新增節流機制 signerSearchHandler() enter條件下的內容
    searchSigner(evt) {
      this.signerLoading = false;
      if (!evt.target.value) return;
      this.getSigner(evt.target.value);
    },
    // 清除遠端搜尋資料
    sidVisibleChange(e) {
      if (!e) {
        this.supplier1 = [];
      }
    },
    sIdSearchHandler(evt) {
      this.sIdLoading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.throttledSIdSearch(evt);
      }
    },
    //2024.03.13 新增節流機制 sIdSearchHandler() enter條件下的內容
    searchSupplier(evt) {
      this.sIdLoading = false;
      if (!evt.target.value) {
        this.supplier1 = [];
      } else {
        this.getSupplier(evt.target.value);
      }
    },
    async getSupplier(query) {
      const res = await this.$store.dispatch("getSupplier", {
        query: query,
        id: this.form.LD1,
      });
      this.supplier1 = res.data.data;
    },
    // 清除遠端搜尋資料
    sid2VisibleChange(e) {
      if (!e) {
        this.supplier2 = [];
      }
    },
    sId2SearchHandler(evt) {
      this.sId2Loading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.throttledSId2Search(evt);
      }
    },
    //2024.03.13 新增節流機制 sId2SearchHandler() enter條件下的內容
    searchSupplier2(evt) {
      this.sId2Loading = false;
      if (!evt.target.value) {
        this.supplier2 = [];
      } else {
        this.getSupplier2(evt.target.value);
      }
    },
    async getSupplier2(query) {
      const res = await this.$store.dispatch("getSupplier2", {
        query: query,
        id: this.form.LD1,
      });
      this.supplier2 = res.data.data;
    },
    async addNewProjectApplication(formGroup) {
      try {
        var res = await this.$store.dispatch(
          "addNewProjectApplication",
          formGroup
        );
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: `成功: ${res.data.data.msg}`,
        });
        this.$router.push({ name: "engineeringDataManagement" });
        if (res.data.data.msg.split(":")[1]) {
          this.projectNumber = res.data.data.msg.split(":")[1];
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getNewBillNo() {
      const res = await this.$store.dispatch("getNewBillNo");
      const data = res.data.data;
      this.fabApprove = data.fab_Approve;
      this.location = data.location;
      this.location_Type = data.location_Type;
      this.work_Type_Class = data.work_Type_Class;
    },
    async getSigner(val) {
      const res = await this.$store.dispatch("getSigner", val);
      const data = res.data.data;
      this.signerList = data;
      this.signerList.forEach((e) => (e.customLabel = `${e.EMP_NO}_${e.NAME}`));
    },
    //2024.03.13 新增節流機制
    throttledSId2Search: null,
    throttledSignerSearch: null,
    throttledSIdSearch: null,
  },
  async mounted() {
    this.getNewBillNo();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    const positionFixed = document.querySelector(".position-fixed");
    const positionFixedHeight = positionFixed.offsetHeight;
    const form = document.querySelector(".form");
    form.style.marginBottom = `${positionFixedHeight}px`;
  },
  //2024.03.13 新增節流機制
  //頁面初始前，先執行的階段
  created() {
    // 使用throttle包裝方法
    this.throttledSId2Search = this.throttle(this.searchSupplier2, 2500, false);
    this.throttledSignerSearch = this.throttle(this.searchSigner, 1500, false);
    this.throttledSIdSearch = this.throttle(this.searchSupplier, 2500, false);
  },
};
</script>

<template src="./newProjectApplication.html"></template>
